import React, { useEffect, useRef, useState } from "react";
import ml5 from "ml5";

let classifier;
let objectDetector;
const delay = 300
// const url = "https://teachablemachine.withgoogle.com/models/RrGhF9eh_/"
// let url = "https://teachablemachine.withgoogle.com/models/c5QHqrAOk/"
// const url = "https://teachablemachine.withgoogle.com/models/zvuTPJFzc/"
// let url ='https://teachablemachine.withgoogle.com/models/lP8sH70Bi/'
// let url = 'https://teachablemachine.withgoogle.com/models/rxnp5FoT0/'
let url ='https://teachablemachine.withgoogle.com/models/ntvbyZsmT/'
url += 'model.json'
// const url = './model.json'

export default function App() {
  const videoRef = useRef();
  const [model , setModel] = useState(url)
  const [result, setResult] = useState([]);
  const [loaded, setLoaded] = useState(false);
  const [input,setInput] = useState(0)
  const [facingMode,setFacingMode] = useState(true)
  useEffect(() => {
    startWebcam()
    if(model == ""){
      setModel(url)
    }else{
      classifier = ml5.imageClassifier(model, () => {
        console.log("Image Model is Loaded")
        console.log(model)
      });
      // objectDetector = ml5.objectDetector(model,()=>{
      //   console.log("object Model is Loaded")
      // } );
    }
     
  }, [model]);
  useEffect(() => {
    if (classifier) {
      if(input === 0){
        setTimeout(() => {
          classifier.classify(videoRef.current, (error, results) => {
            if (error) {
              console.error(error);
              return;
            }
            setResult(results);
          }); 
        }, delay);
      }else if(input === 1){
        setTimeout(() => {
          objectDetector.detect(videoRef.current, (error, results) => {
            if(error) {
              console.error(error)
              return
            }
            console.log(results)
          })
        }, delay);
      }else{
        ;
        // add more here !
      }
     
    }
    console.log(result)
  }, [result,input])
  const startWebcam = async () =>{
     await navigator.mediaDevices
        .getUserMedia({ video: {
          facingMode: facingMode ?  "environment"  :  "user" 
        } })
        .then(stream => videoRef.current.srcObject = stream)
        .catch(console.log);
  }
  const stopWebcam = () =>{
    const stream =  videoRef.current.srcObject;
    stream.getTracks().forEach(function(track) {
      track.stop();
    });
  }
  return (
    <div style={{display: 'flex',flexDirection:'column',alignItems: 'center',justifyContent: 'center'}}>
      <div className="flex" >
        <button type="button" className="btn btn-outline-success" onClick={() => setInput(0)}>Image Classify</button>
        {/* <button type="button" className="btn btn-outline-warning" onClick={() => {setInput(1);}}>Object Detector</button> */}
      </div>
      <video
        ref={videoRef}
        width={window.innerWidth}
        height={window.innerHeight*.75}
        autoPlay
        onClick={() =>{stopWebcam();setFacingMode(prev=>!prev);startWebcam()}}
      ></video> 
      
      {classifier && result && result !== undefined ? 
      <div >
        <h1 >Result</h1>
        {result.map((item, ind) => <p key={ind}>{item.label} 

        <div className="progress">
        <div className="progress-bar" role="progressbar" style={{width:(item.confidence*110)}} aria-valuenow={(item.confidence*100).toFixed(2) } aria-valuemin="0" aria-valuemax="100">{(item.confidence*100).toFixed(2) }%</div>
        </div>
        </p>)}
      </div>
      
      :<>กำลังโหลด ...</> }
      
    </div>
  )
}
